import * as React from 'react';
import {
  Button,
  CompassContent,
  CompassPanel,
  Content,
  Flex,
  FlexItem,
  Label,
  MenuToggle,
  MenuToggleElement,
  SearchInput,
  Select,
  SelectList,
  SelectOption,
} from '@patternfly/react-core';
import {
  CheckCircleIcon,
  ExclamationTriangleIcon,
  ExclamationCircleIcon,
  TimesCircleIcon,
  TimesIcon,
  ArrowRightIcon,
  ExternalLinkAltIcon,
  SecurityIcon,
  InfoCircleIcon,
} from '@patternfly/react-icons';
import { Table, Thead, Tr, Th, Tbody, Td, ThProps } from '@patternfly/react-table';

// Security Feed data
interface SecurityFeedRow {
  cveId: string;
  severity: 'critical' | 'important' | 'moderate' | 'minor' | 'none';
  affectedPackage: string;
  status: 'fixed' | 'affected' | 'not affected';
  published: string;
  lastUpdated: string;
}

const securityFeedData: SecurityFeedRow[] = [
  { cveId: 'CVE-2024-1234', severity: 'critical', affectedPackage: 'openssl-1.1.1', status: 'fixed', published: '2024-01-15', lastUpdated: '2024-01-20' },
  { cveId: 'CVE-2024-5678', severity: 'important', affectedPackage: 'curl-7.88.0', status: 'affected', published: '2024-01-10', lastUpdated: '2024-01-18' },
  { cveId: 'CVE-2024-9012', severity: 'moderate', affectedPackage: 'python-3.11.0', status: 'fixed', published: '2024-01-08', lastUpdated: '2024-01-12' },
  { cveId: 'CVE-2024-3456', severity: 'minor', affectedPackage: 'nodejs-18.0.0', status: 'not affected', published: '2024-01-05', lastUpdated: '2024-01-06' },
  { cveId: 'CVE-2024-7890', severity: 'critical', affectedPackage: 'go-1.21.0', status: 'affected', published: '2024-01-03', lastUpdated: '2024-01-15' },
  { cveId: 'CVE-2024-2345', severity: 'important', affectedPackage: 'nginx-1.24.0', status: 'fixed', published: '2024-01-01', lastUpdated: '2024-01-10' },
  { cveId: 'CVE-2024-6789', severity: 'moderate', affectedPackage: 'redis-7.0.0', status: 'affected', published: '2023-12-28', lastUpdated: '2024-01-05' },
  { cveId: 'CVE-2024-0123', severity: 'none', affectedPackage: 'postgres-15.0', status: 'fixed', published: '2023-12-20', lastUpdated: '2024-01-02' },
];

const SeverityLabel: React.FC<{ severity: SecurityFeedRow['severity'] }> = ({ severity }) => {
  switch (severity) {
    case 'critical':
      return <Label color="red" icon={<SecurityIcon />}>Critical</Label>;
    case 'important':
      return <Label color="orange" icon={<ExclamationCircleIcon />}>Important</Label>;
    case 'moderate':
      return <Label color="yellow" icon={<ExclamationTriangleIcon />}>Moderate</Label>;
    case 'minor':
      return <Label color="grey" icon={<InfoCircleIcon />}>Minor</Label>;
    case 'none':
      return <Label color="blue" icon={<CheckCircleIcon />}>None</Label>;
  }
};

const StatusLabel: React.FC<{ status: SecurityFeedRow['status'] }> = ({ status }) => {
  switch (status) {
    case 'fixed':
      return <Label color="green" icon={<CheckCircleIcon />}>Fixed</Label>;
    case 'affected':
      return <Label color="red">Affected</Label>;
    case 'not affected':
      return <Label color="grey">Not affected</Label>;
  }
};

// Severity order for sorting (critical is highest)
const severityOrder: Record<SecurityFeedRow['severity'], number> = {
  critical: 0,
  important: 1,
  moderate: 2,
  minor: 3,
  none: 4,
};

// Status order for sorting
const statusOrder: Record<SecurityFeedRow['status'], number> = {
  affected: 0,
  'not affected': 1,
  fixed: 2,
};

const SecurityFeed: React.FunctionComponent = () => {
  const [searchValue, setSearchValue] = React.useState('');
  const [isDrawerExpanded, setIsDrawerExpanded] = React.useState(false);
  const [selectedCve, setSelectedCve] = React.useState<SecurityFeedRow | null>(null);
  const [isResultsFilterOpen, setIsResultsFilterOpen] = React.useState(false);
  const [resultsFilter, setResultsFilter] = React.useState<'active' | 'all'>('active');
  const [activeSortIndex, setActiveSortIndex] = React.useState<number | null>(null);
  const [activeSortDirection, setActiveSortDirection] = React.useState<'asc' | 'desc'>('asc');

  const onCveClick = (row: SecurityFeedRow) => {
    setSelectedCve(row);
    setIsDrawerExpanded(true);
  };

  const onCloseDrawer = () => {
    setIsDrawerExpanded(false);
  };

  // Sort handler
  const getSortParams = (columnIndex: number): ThProps['sort'] => ({
    sortBy: {
      index: activeSortIndex ?? undefined,
      direction: activeSortDirection,
    },
    onSort: (_event, index, direction) => {
      setActiveSortIndex(index);
      setActiveSortDirection(direction);
    },
    columnIndex,
  });

  const filteredData = securityFeedData.filter(row => {
    const matchesSearch = row.cveId.toLowerCase().includes(searchValue.toLowerCase()) ||
                          row.affectedPackage.toLowerCase().includes(searchValue.toLowerCase());
    // "Active CVEs" excludes fixed/resolved CVEs
    const matchesResultsFilter = resultsFilter === 'all' || row.status !== 'fixed';
    return matchesSearch && matchesResultsFilter;
  });

  // Sort the filtered data
  const sortedData = [...filteredData].sort((a, b) => {
    if (activeSortIndex === null) return 0;
    
    let comparison = 0;
    if (activeSortIndex === 1) {
      // Severity column
      comparison = severityOrder[a.severity] - severityOrder[b.severity];
    } else if (activeSortIndex === 3) {
      // Status column
      comparison = statusOrder[a.status] - statusOrder[b.status];
    }
    
    return activeSortDirection === 'asc' ? comparison : -comparison;
  });

  return (
    <>
      <CompassContent>
            {/* Hero Banner - asymmetric corners per Compass design */}
            <div 
              className="pf-v6-c-hero"
              style={{
                marginBottom: 'var(--pf-t--global--spacer--lg)',
                backgroundColor: 'rgba(199, 199, 199, 0.25)',
                borderStartStartRadius: '24px',
                borderStartEndRadius: '72px',
                borderEndEndRadius: '24px',
                borderEndStartRadius: '72px',
                padding: 'var(--pf-t--global--spacer--xl)',
              }}
            >
              <div style={{ maxWidth: '600px' }}>
                <Content component="h1" style={{ marginBottom: 'var(--pf-t--global--spacer--md)' }}>
                  Project Hummingbird Container Images
                </Content>
                <Content component="p" style={{ marginBottom: 'var(--pf-t--global--spacer--lg)' }}>
                  Red Hat is excited to introduce <strong>Hummingbird container images</strong>.
                  These container images are near-zero CVE, lightweight images ready 
                  to plug and play with your existing stack.
                </Content>
                <Flex gap={{ default: 'gapMd' }}>
                  <FlexItem>
                    <Button variant="primary">Request an image</Button>
                  </FlexItem>
                  <FlexItem>
                    <Button variant="link" icon={<ArrowRightIcon />} iconPosition="end">
                      Learn more
                    </Button>
                  </FlexItem>
                </Flex>
              </div>
            </div>

            {/* Security Feed Header - floating on background, sticky */}
            <div style={{ 
              position: 'sticky',
              top: 0,
              zIndex: 1,
              paddingBottom: 'var(--pf-t--global--spacer--md)',
            }}>
              <Flex 
                justifyContent={{ default: 'justifyContentSpaceBetween' }} 
                alignItems={{ default: 'alignItemsCenter' }}
                flexWrap={{ default: 'wrap' }}
                gap={{ default: 'gapMd' }}
              >
                <FlexItem>
                  <Content component="h2" style={{ margin: 0 }}>Security Feed</Content>
                </FlexItem>
                <FlexItem>
                  <Flex gap={{ default: 'gapMd' }} alignItems={{ default: 'alignItemsFlexEnd' }}>
                    <FlexItem>
                      <div style={{ display: 'flex', flexDirection: 'column', gap: '4px' }}>
                        <div style={{ height: '18px' }} /> {/* Spacer to align with labeled dropdown */}
                        <SearchInput
                          placeholder="Search"
                          value={searchValue}
                          onChange={(_event, value) => setSearchValue(value)}
                          onClear={() => setSearchValue('')}
                          style={{ width: '200px' }}
                        />
                      </div>
                    </FlexItem>
                    <FlexItem>
                      <div style={{ display: 'flex', flexDirection: 'column', gap: '4px' }}>
                        <label style={{ fontSize: 'var(--pf-t--global--font--size--body--sm)', color: 'var(--pf-t--global--text--color--subtle)' }}>
                          Show results for
                        </label>
                        <Select
                          id="results-filter-select"
                          isOpen={isResultsFilterOpen}
                          selected={resultsFilter}
                          onSelect={(_event, value) => {
                            setResultsFilter(value as 'active' | 'all');
                            setIsResultsFilterOpen(false);
                          }}
                          onOpenChange={setIsResultsFilterOpen}
                          toggle={(toggleRef: React.Ref<MenuToggleElement>) => (
                            <MenuToggle
                              ref={toggleRef}
                              onClick={() => setIsResultsFilterOpen(!isResultsFilterOpen)}
                              isExpanded={isResultsFilterOpen}
                              style={{ minWidth: '200px' }}
                            >
                              {resultsFilter === 'active' ? 'Active CVEs' : 'Active and resolved CVEs'}
                            </MenuToggle>
                          )}
                        >
                          <SelectList>
                            <SelectOption value="active" isSelected={resultsFilter === 'active'}>Active CVEs</SelectOption>
                            <SelectOption value="all" isSelected={resultsFilter === 'all'}>Active and resolved CVEs</SelectOption>
                          </SelectList>
                        </Select>
                      </div>
                    </FlexItem>
                  </Flex>
                </FlexItem>
              </Flex>
            </div>

            {/* Table Panel */}
            <CompassPanel className="pf-m-no-padding">
              <div style={{ padding: 'var(--pf-t--global--spacer--lg)' }}>
                <Table variant="compact" borders={false}>
                  <Thead>
                    <Tr>
                      <Th>CVE ID</Th>
                      <Th sort={getSortParams(1)}>Severity</Th>
                      <Th>Affected package</Th>
                      <Th sort={getSortParams(3)}>Status</Th>
                      <Th>Published</Th>
                      <Th>Last updated</Th>
                    </Tr>
                  </Thead>
                  <Tbody>
                    {sortedData.map((row, index) => (
                      <Tr key={index}>
                        <Td dataLabel="CVE ID">
                          <Flex gap={{ default: 'gapSm' }} alignItems={{ default: 'alignItemsCenter' }}>
                            <FlexItem>
                              <Button 
                                variant="link" 
                                isInline 
                                onClick={() => onCveClick(row)}
                              >
                                {row.cveId}
                              </Button>
                            </FlexItem>
                            <FlexItem>
                              <Button
                                variant="plain"
                                component="a"
                                href={`https://nvd.nist.gov/vuln/detail/${row.cveId}`}
                                target="_blank"
                                aria-label={`View ${row.cveId} on NVD`}
                                style={{ padding: '2px' }}
                              >
                                <ExternalLinkAltIcon style={{ fontSize: '0.875rem' }} />
                              </Button>
                            </FlexItem>
                          </Flex>
                        </Td>
                        <Td dataLabel="Severity">
                          <SeverityLabel severity={row.severity} />
                        </Td>
                        <Td dataLabel="Affected package">
                          {row.affectedPackage}
                        </Td>
                        <Td dataLabel="Status">
                          <StatusLabel status={row.status} />
                        </Td>
                        <Td dataLabel="Published">
                          {row.published}
                        </Td>
                        <Td dataLabel="Last updated">
                          {row.lastUpdated}
                        </Td>
                      </Tr>
                    ))}
                  </Tbody>
                </Table>
              </div>
            </CompassPanel>
      </CompassContent>

      {/* Drawer overlay - positioned fixed to cover entire viewport */}
      {isDrawerExpanded && (
        <>
          {/* Backdrop */}
          <div 
            onClick={onCloseDrawer}
            style={{
              position: 'fixed',
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              backgroundColor: 'rgba(0, 0, 0, 0.5)',
              zIndex: 500,
            }}
          />
          {/* Drawer panel */}
          <div style={{
            position: 'fixed',
            top: 0,
            right: 0,
            bottom: 0,
            width: '400px',
            backgroundColor: 'var(--pf-t--global--background--color--primary--default)',
            boxShadow: 'var(--pf-t--global--box-shadow--lg)',
            zIndex: 501,
            display: 'flex',
            flexDirection: 'column',
          }}>
            {/* Drawer Header */}
            <div style={{
              padding: 'var(--pf-t--global--spacer--lg)',
              borderBottom: '1px solid var(--pf-t--global--border--color--default)',
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'flex-start',
            }}>
              <div>
                <Content component="h2" style={{ margin: 0 }}>
                  {selectedCve?.cveId}
                </Content>
                <Content component="p" style={{ margin: 0, marginTop: 'var(--pf-t--global--spacer--sm)', color: 'var(--pf-t--global--text--color--subtle)' }}>
                  {selectedCve && `${selectedCve.severity.charAt(0).toUpperCase() + selectedCve.severity.slice(1)} severity vulnerability in ${selectedCve.affectedPackage}`}
                </Content>
              </div>
              <Button variant="plain" aria-label="Close drawer" onClick={onCloseDrawer}>
                <TimesIcon />
              </Button>
            </div>
            {/* Drawer Body */}
            <div style={{
              padding: 'var(--pf-t--global--spacer--lg)',
              flex: 1,
              overflowY: 'auto',
            }}>
              <Content component="p" style={{ color: 'var(--pf-t--global--text--color--subtle)' }}>
                Drawer panel body content
              </Content>
            </div>
          </div>
        </>
      )}
    </>
  );
};

export { SecurityFeed };
